body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{overflow:auto;font-family:"Montserrat", sans-serif}button{background:#999;color:#fff;border:none;padding:0.5em;cursor:pointer;margin-bottom:.3em;border-radius:0.2em}button:hover{background:#888}pre{border:1px solid #ccc;background:#f0f0f0;border-radius:0.2em;padding:0.5em;margin:0}pre>pre{background:none;border:none;padding:0}.public-DraftEditorPlaceholder-root{margin-top:20vw;color:#666666;font-size:xx-large;position:absolute;z-index:0;margin-left:20px;vertical-align:center}.public-DraftEditor-content{width:85vw;max-width:800px;height:90vh}.DraftEditor-root{height:90vh}.editorCoontainer{display:flex;justify-content:center;font-size:large;margin-top:40px;height:100vh}

.shareButton{background:white;text-align:center;width:50px;height:50px;border-radius:100%;display:inline-flex;flex-direction:column;justify-content:center;align-items:center;font-size:30px;color:#666666;font-weight:bold;text-decoration:none;box-shadow:5px 10px 20px;float:right;margin-left:80%;bottom:72px;position:fixed}.fabButtonContainer{height:100%;width:100%}

.infoFabButton{background:white;text-align:center;width:50px;height:50px;border-radius:100%;display:inline-flex;flex-direction:column;justify-content:center;align-items:center;font-size:30px;color:#666666;font-weight:bold;text-decoration:none;box-shadow:5px 10px 20px;float:right;margin-left:80%;bottom:12px;position:fixed}.fabButtonContainer{height:100%;width:100%}

.placeholderContainer{display:flex;justify-content:center;pointer-events:none}.placeholder{position:fixed;top:0;width:100vw;max-width:500px}.copyConfirmationContainer{position:fixed;width:100%;display:flex;justify-content:center}.copyConfirmation{display:flex;justify-content:center;align-items:center;box-shadow:3px 19px 46px;border-radius:16px;height:25px;width:70px;padding:5px;top:100px;background:white;box-shadow:4px 4px 6px gray;font-family:"Montserrat", sans-serif}#homeContainer{height:100vh}

.logoContainer{display:flex;justify-content:center}.aboutContainer{display:flex;justify-content:center;font-weight:400;font-size:22px;font-family:"Montserrat", sans-serif}.aboutContent{height:90vh;display:flex;flex-direction:column;justify-content:space-around;padding:0 30px 0 30px}.authors{font-size:18px}a:link{text-decoration:none}


